import { FormControl, InputLabel, MenuItem, Select, Tooltip } from "@mui/material";

type Props = {
  title: string;
  tooltip: string;
  value: string;
  fullWidth?: boolean;
  defaultValue?: string;
  values: string[];
  labels?: string[];
  setter?: (value: string) => void;
  onChange?: () => void;
}

export const SelectList = ({ title, tooltip, value, fullWidth, values, labels, defaultValue, setter, onChange }: Props) => {
  return (
    <Tooltip followCursor={true} title={tooltip} >
      <FormControl sx={{ flex: 1 }} variant="outlined" fullWidth={fullWidth}>
        <InputLabel id={title} sx={{zIndex: 0}}>{title}</InputLabel>
        <Select
          labelId={title}
          value={value}
          label={title}
          onChange={(e) => {
            if (setter) setter(e.target.value as string);
            if (onChange) onChange();
          }}>
          {defaultValue && <MenuItem value="">{defaultValue}</MenuItem>}
          {values.map((v, index) => (<MenuItem key={v} value={v}>{labels ? labels[index] : v}</MenuItem>))}
        </Select>
      </FormControl>
    </Tooltip>
  );
};