
import { Box, Button, Typography, TextField, useTheme, Paper, InputBase } from '@mui/material';
import { DropdownSelection, MultiDropdownSelection } from "../input/InputFields";
import { SelectList } from '../input/SelectList';
import { modCategories, resourcepackCategories, features, indexValues, minecraftVersions, modLoaders, resolutions, environments, maxResults, shaderCategories, shaderFeatures, shaderLoader, performanceImpact } from '../../Constants';
import { ItemCard } from '../items/ItemCard';
import { useItemInfo } from '../items/ItemInfo';
import { SearchItem } from '../../models/Search';
import { showSnackbar } from '../providers/SnackbarProvider';
import { useModcContext } from '../providers/modcContext';

type Results = {
    hits: SearchItem[];
    total_hits: number;
}

type Props = {
    type: string;
    version: string;
    loader: string;
    query: string;
    index: string;
    page: number;
    categories: string[];
    environment: boolean[];
    results: Results;
    setType: (arg0: string) => void;
    setQuery: (arg0: string) => void;
    setIndex: (arg0: string) => void;
    setPage: (arg0: number) => void;
    setCategories: (arg0: string[]) => void;
    setEnvironment: (arg0: boolean[]) => void;
}

const SearchComponent = ({type, version, loader, query, index, page, categories, environment, results, setType, setQuery, setIndex, setPage, setCategories, setEnvironment}: Props) => {
    const theme = useTheme();
    const { showItemInfo, ItemInfoComponent } = useItemInfo();
    const { setVersion, setLoader } = useModcContext();
    return (
        <Box sx={{ display: 'flex', flexDirection: 'row', gap: '1w', mt: 3, maxWidth: "100vw", padding: "15px"}}>
            <Paper elevation={5} sx={{ display: 'flex', flexDirection: 'column', maxWidth: '45vw', width: "200px", minWidth: "160px", background: theme.palette.background.paper, borderRadius: '8px', padding: '16px', gap: "5px", overflowY: "overlay", maxHeight: "88vh" }}>
                <Box sx={{display:"flex", flexDirection: "column", gap: "10px"}}>
                    <SelectList title="Version" tooltip="Minecraft version" value={version} values={minecraftVersions} setter={(v)=>setVersion(v)} />
                    <SelectList title="Type" tooltip="Project type" value={type} values={["mod", "resourcepack", "shader"]} setter={setType} />
                    {type === 'mod' ? (<>
                        <SelectList title="Loader" tooltip="Minecraft mod loader" value={loader} values={modLoaders} setter={(v)=>setLoader(v)} />
                        <MultiDropdownSelection options={modCategories} label="Categories" categoryLabel="categories" categorySetter={setCategories} categories={categories} />
                        <DropdownSelection title='Environment' options={environments} bools={environment} setBools={setEnvironment} />
                    </>) : type === 'resourcepack' ? (<>
                        <MultiDropdownSelection options={resourcepackCategories} label="Categories" categories={categories} categoryLabel="categories" categorySetter={setCategories} />
                        <MultiDropdownSelection options={features} label="Features" categories={categories} categoryLabel="categories" categorySetter={setCategories} />
                        <MultiDropdownSelection options={resolutions} label="Resolutions" categories={categories} categoryLabel="categories" categorySetter={setCategories} />
                    </>) : <>
                        <MultiDropdownSelection options={shaderCategories} label="Categories" categories={categories} categoryLabel="categories" categorySetter={setCategories} />
                        <MultiDropdownSelection options={shaderFeatures} label="Features" categories={categories} categoryLabel="categories" categorySetter={setCategories} />
                        <MultiDropdownSelection options={performanceImpact} label="Performance impact" categories={categories} categoryLabel="categories" categorySetter={setCategories} />
                        <MultiDropdownSelection options={shaderLoader} label="Loader" categories={categories} categoryLabel="categories" categorySetter={setCategories} />
                    </>}
                </Box>
            </Paper>
            <Box sx={{display: 'flex', flexDirection: 'column', maxWidth: '88vw', width:"1680px", alignItems: 'center', padding: '0 2.9vw', justifyContent: "start" }}>
                <Box sx={{display: 'flex', flexDirection: 'row', gap: '2%', width: '99%'}}>
                    <TextField placeholder='Search' value={query} onChange={(e) => setQuery(e.target.value)} sx={{ width: '86%', maxWidth: '60vw' }}/>
                    <SelectList title="Index" tooltip="Sort by" value={index} values={indexValues} setter={setIndex} />
                </Box>
                <Box sx={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center', gap: '20px', mt: 4, overflowY: "overlay", maxHeight: "81vh", padding: "5px", width: '100%' }}>
                    {results.hits && results.hits.length > 0 ? (results.hits.map((item) => (
                        <ItemCard key={item.project_id} search showInfo={showItemInfo} item={item} />))) : (<Typography variant="body1" color="textSecondary">No results</Typography>)}
                </Box>
                <Box sx={{ display: 'flex', position: 'fixed', flexDirection: 'row', alignItems: 'center', gap: 1, bottom: 16 }}>
                    <InputBase
                        startAdornment={<Button onClick={() => page === 1 ? setPage(Math.ceil(results.total_hits / maxResults)) : setPage(page - 1)}>&lt;</Button>}
                        endAdornment={<>-{Math.ceil(results.total_hits / maxResults)}<Button onClick={() => page === Math.ceil(results.total_hits / maxResults) ? setPage(1) : setPage(page + 1)}>&gt;</Button></>}
                        sx={{ width: `${page.toString().length * 9.8 + Math.ceil(results.total_hits / maxResults).toString().length * 9.8 + 130 }px`, padding: 0}} value={page} onChange={(e) => {if (!/\D/.test(e.target.value)) setPage(Math.min(Math.max(Number(e.target.value), 1), Math.ceil(results.total_hits / maxResults)));}}/>
                </Box>
            </Box>
            {ItemInfoComponent}
        </Box>
    )
}

export default SearchComponent;