import { Accordion, AccordionDetails, AccordionSummary, Checkbox, FormControl, FormControlLabel, FormGroup, InputLabel, MenuItem, Select, Tooltip, Typography } from "@mui/material";

type MultiDropdownSelectionProps = {
  label: string;
  categoryLabel: string;
  options: string[];
  categories: string[];
  categorySetter: (value: string[]) => void;
}

export const MultiDropdownSelection = ({ options, categories, categoryLabel, label, categorySetter }: MultiDropdownSelectionProps) => {
  return (
    <Accordion>
      <AccordionSummary expandIcon={">"}>
        <Typography>{label}</Typography>
      </AccordionSummary>
      <AccordionDetails>
        <FormGroup>
          {options.map((cat) => (
            <FormControlLabel
              key={cat}
              label={cat}
              control={
                <Checkbox
                  checked={categories.includes(`${categoryLabel}:${cat.toLowerCase()}`)}
                  onChange={(e) => e.target.checked ? categorySetter([...categories, `${categoryLabel}:${cat.toLowerCase()}`]) : categorySetter(categories.filter(param => param !== `${categoryLabel}:${cat.toLowerCase()}`))}
                />
              }
            />
          ))}
        </FormGroup>
      </AccordionDetails>
    </Accordion>
  );
};

type DropdownSelectionProps = {
  title: string;
  options: string[];
  bools: boolean[];
  setBools: (arg0: boolean[]) => void;
}

export const DropdownSelection = ({title, options, bools, setBools}: DropdownSelectionProps ) => {
  return (
      <Accordion>
          <AccordionSummary expandIcon={">"}>
              <Typography>{title}</Typography>
          </AccordionSummary>
          <AccordionDetails>
              <FormGroup>
                  {options.map((label, index) => (
                      <FormControlLabel
                          key={label}
                          control={
                              <Checkbox
                                  checked={bools[index]}
                                  onChange={() => setBools(bools.map((value, i) => (i === index ? !value : value)))}
                              />
                          }
                          label={label}
                      />
                  ))}
              </FormGroup>
          </AccordionDetails>
      </Accordion>
  );
};
