import { createContext, useContext, useEffect, useState } from "react";
import { GetParam, SetParam } from "../methods/GetSetParam";
import { minecraftVersions, modLoaders } from "../../Constants";
import { decodeItems, encodeItems } from "../methods/DecodeEncodeItems";
import { ItemType } from "../../models/Item";
import { ErrorInfo, ErrorType } from "../../models/Errors";

type ItemInfo = {
  items: [string, string?][];
  type: ItemType;
  add: (item: [string, string?]) => boolean;
  remove: (item: [string, string?]) => void;
  set: (items: [string, string?][]) => void;
  setVersion: (id: string, version: string) => void;
  getVersion: (id: string) => string;
  clearVersions: () => void;
};

type ModcContextType = {
  tab: string;
  version: string;
  loader: string;
  error: ErrorInfo;
  setTab: (arg0: string) => void;
  setVersion: (arg0: string) => void;
  setLoader: (arg0: string) => void;
  getItem: (itemType: ItemType) => ItemInfo;
};

const modcContext = createContext<ModcContextType | null>(null);

export function ModcProvider({ children }: { children: React.ReactNode }) {
  const [tab, setTab] = useState(GetParam("tab") || 'modc');
  const [version, setVersion] = useState(GetParam("version") || minecraftVersions[0]);
  const [loader, setLoader] = useState(GetParam("loader") || modLoaders[0]);
  const [errors, setErrors] = useState<ErrorType[]>([]);
  const initialItems: Record<ItemType, [string, string?][]> = {
    mod: decodeItems(GetParam('mods')) || [],
    resourcepack: decodeItems(GetParam('resourcepacks')) || [],
    shader: decodeItems(GetParam('shaders')) || [],
    datapack: decodeItems(GetParam('datapacks')) || [],
  };

  const [items, setItems] = useState<Record<ItemType, [string, string?][]>>(initialItems);

  useEffect(() => {
    SetParam("version", version);
    SetParam("loader", loader);
    SetParam("mods", encodeItems(items.mod));
    SetParam("resourcepacks", encodeItems(items.resourcepack));
    SetParam("shaders", encodeItems(items.shader));
    SetParam("datapacks", encodeItems(items.datapack));
    SetParam("tab", tab);
  }, [tab, version, loader, items]);

  useEffect(() => {
    clearItemVersions('mod');
    setErrors([]);
  }, [version, loader]);

  const setItem = (itemType: ItemType, list: [string, string?][]) => setItems(prev => ({ ...prev, [itemType]: list }));
  const addItem = (itemType: ItemType, item: [string, string?]) => {
    if (items[itemType].some(i => i[0] === item[0])) return false;
    setItems(prev => ({ ...prev, [itemType]: [...prev[itemType], item] }));
    return true;
  }
  const removeItem = (itemType: ItemType, item: [string, string?]) => {
    setItems(prev => ({ ...prev, [itemType]: prev[itemType].filter(i => i[0] !== item[0]) }));
  };
  const clearItemVersions = (itemType: ItemType) => setItems(prev => ({ ...prev, [itemType]: prev[itemType].map(i => [i[0]]) }));

  const getItem = (itemType: ItemType): ItemInfo => ({
    items: items[itemType],
    type: itemType,
    add: (item) => addItem(itemType, item),
    remove: (item) => removeItem(itemType, item),
    set: (list) => setItem(itemType, list),
    getVersion: (id: string) => {
      const item = items[itemType].find(i => i[0] === id);
      if (!item || !item[1]) return "";
      return item[1];
    },
    setVersion: (id: string, version: string) => {
      setItems(prev => ({ ...prev, [itemType]: prev[itemType].map(i => i[0] === id ? ([id, version] as [string, string?]) : i) }));
    },
    clearVersions: () => clearItemVersions(itemType),
  });

  const error: ErrorInfo = {
    errors,
    addError: (err: ErrorType) => setErrors((prev) => prev.some(e => e.id === err.id) ? prev : [...prev, err]),
    removeError: (id: string) => setErrors((prev) => prev.filter((e) => e.id !== id)),
  };

  return (
    <modcContext.Provider value={{ tab, version, loader, error, setTab, setVersion, setLoader, getItem }}>
      {children}
    </modcContext.Provider>
  );
}

export const useModcContext = () => {
  const context = useContext(modcContext);
  if (!context) {
    throw new Error("useModcContext must be used within a ModcProvider");
  }
  return context;
};