import { Box, Button, useTheme } from "@mui/material";
import { useEffect } from "react";
import { Items, ItemType } from "../../models/Item";
import { ItemCard } from "../items/ItemCard";
import { GetParam } from "../methods/GetSetParam";
import Errors from "../Errors";
import { useModcContext } from "../providers/modcContext";

type VersionComponentProps = {
    type: ItemType;
    show: boolean;
    items: Items[];
    showItemInfo: (arg0: string, arg2: ItemType) => void;
}

const VersionComponent = ({ items, show, type, showItemInfo } : VersionComponentProps) => {
    const { error } = useModcContext();
    const ItemComponent = ({version, loader, item} : {version?: string, loader?:string, item: Items}) => {
        const hasError = error.errors.some(e => e.id === item.id && e.type === type);

        useEffect(() => {
            if (!error.errors.some(e => e.id === item.id && e.type === type)) {
                if (version && !item.game_versions.includes(version)) {
                    error.addError({ id: item.id, type: type, slug: item.slug, message: `does not exist for version ${version}` });
                } else if (type === 'mod' && loader && !item.loaders.includes(loader)) {
                    error.addError({ id: item.id, type: type, slug: item.slug, message: `does not exist for loader ${loader}` });
                }
            }
        }, [version, loader, item.id, item.game_versions, item.loaders]);

        return <ItemCard search={false} color={hasError ? "error" : ""} showInfo={showItemInfo} item={{project_id: item.id, title: item.title, project_type: type, icon_url: item.icon_url, slug: item.slug}}/>
    }

    return (
        <Box sx={{ display: (show ? 'flex' : 'none'), flexDirection:'column', alignItems: 'center'}}>
            {(items && items.length > 0) && (<>
            <Errors type={type} />
            <Box sx={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center', gap: '20px', width: '85vw', borderRadius: 2, paddingBottom: 1}}>
                {(items.length > 0 && items.map((item) => <ItemComponent key={item.id} version={GetParam('version')} loader={GetParam('loader')} item={item} />))}
            </Box></>)}
        </Box>
    );
}

export default VersionComponent;