import { Box, Typography, Tooltip, Button, LinearProgress } from "@mui/material";
import { minecraftVersions, modLoaders } from "./../Constants";
import { SelectList } from './input/SelectList';
import VersionContainer from "./versions/Container";
import { downloadItems } from "./methods/Download";
import { useState } from "react";
import { showSnackbar } from "./providers/SnackbarProvider";
import SmoothSwitchBar from "./smoothSwitchBar";
import { ItemGroup } from "../models/Item";
import { useModcContext } from "./providers/modcContext";

const ModC = () => {
  const { version, loader, getItem, setVersion, setLoader, error } = useModcContext();
  const modInfo = getItem('mod');
  const resourcepackInfo = getItem('resourcepack');
  const shaderInfo = getItem('shader');
  const datapackInfo = getItem('datapack');

  const [downloading, setDownloading] = useState(false);
  const [progress, setProgress] = useState(-1);
  const [selected, setSelected] = useState("mod");

  const DownloadAll = async () => {
    const items: ItemGroup[] = [
      ...(modInfo.items.length > 0 ? [{ label: "mods", items: modInfo.items }] : []), ...(resourcepackInfo.items.length ? [{ label: "resourcepacks", items: resourcepackInfo.items }] : []),
      ...(shaderInfo.items.length > 0 ? [{ label: "shaders", items: shaderInfo.items }] : []), ...(datapackInfo.items.length ? [{ label: "datapacks", items: datapackInfo.items }] : []),
    ];
    downloadItems(loader, version, items, setDownloading, setProgress, showSnackbar);
  }

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', gap: '20px', alignItems: 'center', width: '100vw', mt: '10px' }}>
      <Typography variant="h2" sx={{textAlign: "center", width: '600px', maxWidth: '90vw'}}>Modpack Downloader</Typography>
      <Typography sx={{ textAlign: "center", width: '600px', maxWidth: '90vw', color: 'text.secondary' }}>Web based modpack manager for minecraft using the Modrinth API</Typography>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', gap: '20px', width: '700px', maxWidth: '90vw' }}>
        <SelectList title="Version" tooltip="Minecraft version" value={version} values={minecraftVersions} setter={(v)=>setVersion(v)} onChange={() => { resourcepackInfo.clearVersions(); modInfo.clearVersions(); shaderInfo.clearVersions(); datapackInfo.clearVersions(); }} />
        <SelectList title="Loader" tooltip="Minecraft mod loader" value={loader} values={modLoaders} setter={(v)=>setLoader(v)} onChange={() => modInfo.clearVersions()} />
      </Box>
      <Tooltip title="Download all mods and resourcepacks of the selected version">
        <Button variant='contained' sx={{ mt: 2, width: '600px', maxWidth: '90vw' }} onClick={() => DownloadAll()} disabled={error.errors.length > 0 || downloading}>{downloading ? 'Downloading...' : 'Download'} </Button>
      </Tooltip>
      <LinearProgress variant="determinate" value={progress} sx={{ width: '600px', maxWidth: '90vw', marginTop: '10px' }} />
      <SmoothSwitchBar options={[{value: "mod", label: 'Mods'}, {value: "resourcepack", label: 'Resourcepacks'},{value: "shader", label: 'Shaders'}]} value={selected} onChange={setSelected} />
      <VersionContainer show={selected === 'mod'} type='mod'></VersionContainer>
      <VersionContainer show={selected === 'resourcepack'} type='resourcepack'></VersionContainer>
      <VersionContainer show={selected === 'shader'} type='shader'></VersionContainer>
      <VersionContainer show={selected === 'datapack'} type='datapack'></VersionContainer>
    </Box>
  );
}

export default ModC;