import { Container, Box, Button } from '@mui/material';
import ModC from './components/modc';
import SearchContainer from './components/search/Container';
import { useModcContext } from './components/providers/modcContext';

const App = () => {
  const {tab, setTab} = useModcContext();
  return (
    <Container sx={{ maxHeight: '100vh', display: 'flex', flexDirection: 'column', alignItems: 'center', paddingTop: 2}}>
      <Box sx={{ display: 'flex', gap: 2}}>
        <Button variant={tab === "modc" ? "outlined" : "contained"} onClick={() => setTab("modc")}>ModC</Button>
        <Button variant={tab === "search" ? "outlined" : "contained"} onClick={() => setTab("search")}>Search</Button>
      </Box>
      <Box sx={{ flex: 1, width: '100%', display: 'flex', justifyContent: 'center' }}>
        {tab === "search" ? <SearchContainer/> : 
          <ModC/>}
      </Box>
    </Container>
  );
};

export default App;
