import { useEffect, useState } from 'react';
import { Box, Breadcrumbs, Divider, Tooltip, Card, Backdrop, CardMedia, CardContent, Typography, IconButton, Button, useTheme, CardHeader, Chip, Tab, Tabs, Paper, Link } from '@mui/material';
import { Close, Download, FavoriteBorder, OpenInNew, Search, Style } from '@mui/icons-material';
import { Item } from '../../models/Search';
import { GetParam } from '../methods/GetSetParam';
import { SelectList } from '../input/SelectList';
import { Dependency, Files, Project } from '../../models/Dependencies';
import { formatter } from '../methods/NumberFormat';
import { useModcContext } from '../providers/modcContext';
import { showSnackbar } from '../providers/SnackbarProvider';
import DependencyComponent from '../Dependencies';
import OverflowBox from '../methods/OverflowBox';
import { ItemType } from '../../models/Item';
import MuiMarkdown, { defaultOverrides } from 'mui-markdown';
import { Masonry } from '@mui/lab';

type Props = {
  data: Item;
  open: boolean;
  onClose: () => void;
  type: ItemType;
}

export const ItemInfo = ({ data, open, onClose, type } : Props) => {
  const [versions, setVersions] = useState<string[]>([""]);
  const { getItem, error } = useModcContext();
  const [version, setVersion] = useState<string>(getItem(type as any).getVersion(data.id) || "");
  const [versionLabels, setVersionLabels] = useState<string[]>([""]);
  const [dependencies, setDependencies] = useState<string[][]>();
  const [tab, setTab] = useState(0);
  const [files, setFiles] = useState<Files[]>([]);
  const theme = useTheme();
  const [added, setAdded] = useState<boolean>(false);
  const itemInfo = getItem(type);
  
  const removeItem = () => {
    itemInfo.remove([data.id]);
    error.removeError(data.id);
    showSnackbar('Item removed', 'success');
    onClose();
  }

  const addItem = () => {
    itemInfo.add([data.id]) ? showSnackbar('Item added', 'success') : showSnackbar('Item already added', 'warning');
    setAdded(true);
  }

  useEffect(() => {
    if (!data) return;
    const fetchData = async () => {
      await fetch(`https://api.modrinth.com/v2/project/${data.id}/version?game_versions=["${GetParam("version")}"]${data.project_type === 'mod' && `&loaders=["${GetParam("loader")}"`}]`)
        .then((response) => {
          if (!response.ok) throw new Error(`HTTP error! Status: ${response.status}`);
          return response.json();
        })
        .then((fetchedData: Project[]) => {
          setVersions(fetchedData.map((item) => item.id));
          setVersionLabels(fetchedData.map((item) => item.version_number));
          setFiles(fetchedData.map((item) => item.files ?? []).flat());
          setDependencies((fetchedData.find((item) => item.id === version)?.dependencies ?? fetchedData[0]?.dependencies ?? []).map((dependency: Dependency) => [dependency.project_id,dependency.dependency_type]).sort((a, b) => a[0].localeCompare(b[0])));
        })
        .catch((error) => console.error("Failed to fetch versions:", error));
    }
    fetchData();
    setAdded(itemInfo.items.some(i => i[0] === data.id));
  }, [version]);

  useEffect(() => {
    getItem(type as any).setVersion(data.id, version);
  }, [version]);


  return (
    <Backdrop open={open} onClick={onClose} sx={{zIndex: 5}}>
      <Card sx={{ padding: 2, position: 'relative', width: '50vw', maxHeight: "90vh", overflowY: 'auto' }} onClick={(e) => e.stopPropagation()} >
        <CardHeader
          avatar={<CardMedia component="img" onClick={() => window.open(`https://modrinth.com/${data.project_type}/${data.slug}`, '_blank')} image={data.icon_url} sx={{ borderRadius: '16px', width: '180px' }} />}
          action={<IconButton onClick={onClose}><Close /></IconButton>} title={<Typography variant='h4'>{data.title}</Typography>}
          subheader={<Box sx={{display: 'flex', justifyContent: 'space-between', flexDirection: 'column', height: '130px', mb: 0.5}}>
            <Box>
              <Typography variant="subtitle1" color="textSecondary" lineHeight={1.2} mb={1} ml={0.6}>{data.description}</Typography>
              <Breadcrumbs separator={<Divider orientation="vertical" sx={{ height: 16 }} />} sx={{ fontSize: 14 }}>
                <Typography sx={{ display: 'flex', alignItems: 'center' }} color='text.secondary' variant='subtitle2'><Download sx={{ margin:0 }}/>{formatter.format(data.downloads)}</Typography>
                <Typography sx={{ display: 'flex', alignItems: 'center' }} color='text.secondary' variant='subtitle2'><FavoriteBorder />{formatter.format(data.followers)}</Typography>
                <Box sx={{ display: 'flex', alignItems: 'center', textTransform: 'capitalize' }}>
                  <Style/>
                  {data.categories.map((category, ) => (
                    <Chip key={category} label={<Typography color='text.secondary' variant='subtitle2'>{category}</Typography>} size='small' sx={{ ml: 0.5, fontSize: 12 }} />
                  ))}
                </Box>
              </Breadcrumbs>
            </Box>
            <Box display='flex' gap={2}>
              <Button variant={theme.palette.mode === "light" ? "contained" : "outlined"} onClick={added ? removeItem : addItem} fullWidth color={added ? 'error' : 'success' }>{added ? 'remove' : 'add'}</Button>
              <Tooltip title={(() => {
                if (files.length <= 0) return 'No info';
                const file = (files[versions.indexOf(version !== '' ? version : versions[0])] || null);
                return `Download ${file.filename} (${(file.size / (1024 * 1024)).toFixed(2)} MB)`;
              })()}>
                <Button variant={theme.palette.mode === "light" ? "contained" : "outlined"} fullWidth onClick={() => {window.open(files[versions.indexOf(version !== '' ? version : versions[0])].url, '_blank'); showSnackbar('Downloading...','success')}}>Download</Button>
              </Tooltip>
            </Box>
          </Box>}
        />
        <CardContent sx={{pt: 0}}>
          <SelectList title="Version" tooltip="Version" fullWidth value={version} labels={versionLabels} values={versions} setter={(v) => setVersion(v)} defaultValue='newest'></SelectList>
          <Divider sx={{mb: 2, mt: 2}} />
          <Tabs sx={{display: 'flex', width: '100%', mb: 2}} value={tab} onChange={(event: React.SyntheticEvent, newValue: number) => setTab(newValue)}>
            <Tab sx={{width: '33.34%'}} label="Description"/>
            <Tab sx={{width: '33.33%'}} label="Info"/>
            <Tab sx={{width: '33.33%'}} label="Galary"/>
          </Tabs>
          {tab === 0 && (<Box >
            <Divider sx={{my: 2}} />
            <MuiMarkdown overrides={{...defaultOverrides, 
              h1: { component: 'h2', props: { style: { marginBottom: 0}} },
              h2: { component: 'h3', props: { style: { marginBottom: 0}} },
              h3: { component: 'h4', props: { style: { marginBottom: 0}} },
              h4: { component: 'h5', props: { style: { marginBottom: 0}} },
              hr: { component: Divider, props: { sx: { my: 2}}},
              details: { props: { style: { display: 'block', padding: '8px 12px', borderRadius: '8px', backgroundColor: theme.palette.mode === 'dark' ? '#4f4f4f' : '#f5f5f5' }}},
              p: { props: { style: { color: theme.palette.text.secondary, marginBottom: 2, marginTop: 0.5}}},
              a: { component: (props: any) => (<Link {...props} target="_blank" rel="noopener noreferrer" underline="hover">{props.children}</Link>)},
              code: { component: Paper, props: { sx: { fontSize: 12, padding: 0.5, px: 0.7, borderRadius: 2, fontFamily: 'Source Code Pro, monospace', bgcolor: theme.palette.mode === 'dark' ? '#2d2d2d' : '#e0e0e0', display: 'inline-block', width: 'auto' }}},
              img: { props: { style: { maxWidth: '100%', height: 'auto' }}}
            }}>{data.body}</MuiMarkdown>
          </Box>)}
          {tab === 1 && (<Box>
            <Divider sx={{my: 2}} />
            <Box sx={{ display: 'flex', flexDirection: 'row', gap: '10px', marginBottom: 1 }}>
              <OverflowBox label='Loaders' values={data.loaders} />
              <OverflowBox label='Versions' values={[...data.game_versions].reverse()} />
              <OverflowBox label='Environment' values={[`Client ${data.client_side}`, `Server ${data.server_side}`]}/>
            </Box>
            <Divider sx={{my: 2}} />
            {(data.issues_url || data.source_url || data.discord_url || data.wiki_url) && (<>
              <Typography variant="h5">Links</Typography>
              <Breadcrumbs separator={<Divider orientation="vertical" flexItem sx={{ mx: 1, height: 16 }} />} aria-label="breadcrumb">
                {data.issues_url && <Typography onClick={() => window.open(data.issues_url, '_blank')} sx={{ display: 'flex', alignItems: 'center' }}>Issues <OpenInNew/></Typography>}
                {data.source_url && <Typography onClick={() => window.open(data.source_url, '_blank')} sx={{display:'flex', alignItems: 'center'}}>Source <OpenInNew/></Typography>}
                {data.wiki_url && <Typography onClick={() => window.open(data.wiki_url, '_blank')} sx={{display:'flex', alignItems: 'center'}}>Wiki <OpenInNew/></Typography>}
                {data.discord_url && <Typography onClick={() => window.open(data.discord_url, '_blank')} sx={{display:'flex', alignItems: 'center'}}>Discord <OpenInNew/></Typography>}
              </Breadcrumbs>
              <Divider sx={{my: 2}} /></>)}
              {dependencies && dependencies?.length > 0 && (<>
              <Typography gutterBottom variant='h5'>Dependencies</Typography>
              <Box sx={{ display: 'flex', flexDirection: 'row', gap: '20px', flexWrap: 'wrap' }}><DependencyComponent items={dependencies} /></Box>
            <Divider sx={{my: 2}} /></>)} 
          </Box>)}
          {tab === 2 && (<Box>
            <Divider sx={{my: 2}} />
            {data.gallery.length > 0 ?
              <Masonry columns={2} spacing={2}>
                {data.gallery.map((item) => (
                  <CardMedia
                    key={item.title}
                    component='img'
                    image={item.url}
                    sx={{ borderRadius: '16px', width: '100%' }}
                  />
                ))}
              </Masonry>: <Typography>No images</Typography>}
          </Box>)}
        </CardContent>
      </Card>
    </Backdrop>
  );
};


export const useItemInfo = () => {
  const [selectedItem, setSelectedItem] = useState<string | null>(null);
  const [selectedType, setSelectedType] = useState<ItemType>('mod');
  const [data, setData] = useState<Item | null>(null);

  useEffect(() => {
    setData(null);
    const fetchProjectData = async (id: string) => {
      try {
        const response = await fetch(`https://api.modrinth.com/v2/project/${id}`);
        const projectData = await response.json();
        setData(projectData);
      } catch (error) {
        console.error(error);
      }
    };

    if (selectedItem) fetchProjectData(selectedItem);
  }, [selectedItem]);

  const showItemInfo = (id: string, type: ItemType) => {
    console.debug('[useItemInfo] showItemInfo called', id, type);
    setSelectedItem(id);
    setSelectedType(type);
  };

  const closeItemInfo = () => setSelectedItem(null);
  const ItemInfoComponent = selectedItem && data != null? (
    <ItemInfo data={data} open={!!selectedItem} onClose={closeItemInfo} type={selectedType} />
  ) : null;

  return { showItemInfo, ItemInfoComponent };
};
