import { Box, ToggleButton, ToggleButtonGroup, SxProps, Theme, Paper, Chip } from "@mui/material";

interface SmoothSwitchBarProps {
  options: {
    label: React.ReactNode;
    value: string;
    sx?: SxProps<Theme>;
  }[];
  value: string;
  onChange: (value: string) => void;
  indicatorColor?: string;
  backgroundColor?: string;
}

const SmoothSwitchBar = ({options, value, onChange}: SmoothSwitchBarProps) => (
  <Paper sx={{width: "100%", position: "relative", padding: 1, borderRadius: 8}}>
    <ToggleButtonGroup value={value} exclusive onChange={(event: React.SyntheticEvent, newValue: string) => onChange(newValue)} fullWidth sx={{ gap:3,height: 50, borderRadius: 25, overflow: "hidden", position: "relative", p: 0}}>
      <Chip color="primary" sx={{ fontWeight: 400, fontSize: 16, zIndex: 1, position: "absolute", top: 0,
        left: `${(options.findIndex((o) => o.value === value) * 101) / options.length}%`, width: `${98 / options.length}%`, height: "100%",
        borderRadius: 25, transition: "left 300ms ease" }} label={(value+"s").toUpperCase()} />
      {options.map((option) => (<ToggleButton key={option.value} value={option.value} sx={{ flex: 1, border: "none" }}>{option.label}</ToggleButton>))}
    </ToggleButtonGroup>
  </Paper>
);


export default SmoothSwitchBar;