import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import { createTheme, CssBaseline, ThemeProvider } from '@mui/material';
import { SnackbarProvider } from './components/providers/SnackbarProvider';
import { CookiesProvider } from 'react-cookie';
import { ModcProvider } from './components/providers/modcContext';

const lightTheme = createTheme({
  palette: {
    mode: 'light',
    primary: {
      main: '#1976d2',
    },
    secondary: {
      main: '#388e3c',
    },
    background: {
      default: '#f5f5f5',
      paper: '#ffffff',
    },
  },
  components: {
    MuiCssBaseline: {
      styleOverrides: {
        body: {
          height: '100vh',
          maxWidth: '100vw',
          '::-webkit-scrollbar': {
            width: '0',    
          },
          scrollbarWidth: 0,
        },
        '::-webkit-scrollbar': {
          width: '8px',
          height: '8px',
          marginLeft: "-5px",
        },
        '::-webkit-scrollbar-thumb': {
          backgroundColor: '#bbb',
          borderRadius: '10px',
        },
        '::-webkit-scrollbar-track': {
          backgroundColor: 'rgba(0, 0, 0, 0)',
        },
        scrollbarWidth: 'thin',
        scrollbarColor: '#555 transparent',
      },
    },
    MuiToggleButtonGroup: {
      styleOverrides: {
        grouped: {
          '&:not(:last-of-type)': {
            borderTopLeftRadius: 25,
            borderBottomLeftRadius: 25,
            borderTopRightRadius: 25,
            borderBottomRightRadius: 25,
            margin: 0,
          },
          '&:not(:first-of-type)': {
            borderTopLeftRadius: 25,
            borderBottomLeftRadius: 25,
            borderTopRightRadius: 25,
            borderBottomRightRadius: 25,
            margin: 0,
          },
        },
      },
    },
  },
});

const darkTheme = createTheme({
  palette: {
    mode: 'dark',
    primary: {
      main: '#90caf9',
    },
    secondary: {
      main: '#55fb86',
    },
    background: {
      default: '#424242',
      paper: '#333333',
    },
  },
  components: {
    MuiCssBaseline: {
      styleOverrides: {
        '::-webkit-scrollbar': {
          width: '8px',
          height: '8px',
        },
        '::-webkit-scrollbar-thumb': {
          backgroundColor: '#555',
          borderRadius: '10px',
        },
        '::-webkit-scrollbar-track': {
          backgroundColor: 'rgba(0, 0, 0, 0)',
        },
        scrollbarWidth: 'thin',
        scrollbarColor: '#555 transparent',
        body: {
          maxHeight: '100vh',
          maxWidth: '100vw',
          scrollbarWidth: '0',
          '::-webkit-scrollbar': {
            width: '0px',
            height: '0px',
          },
        },
      },
    },
    
    MuiToggleButtonGroup: {
      styleOverrides: {
        grouped: {
          '&:not(:last-of-type)': {
            borderTopLeftRadius: 25,
            borderBottomLeftRadius: 25,
            borderTopRightRadius: 25,
            borderBottomRightRadius: 25,
            margin: 0,
          },
          '&:not(:first-of-type)': {
            borderTopLeftRadius: 25,
            borderBottomLeftRadius: 25,
            borderTopRightRadius: 25,
            borderBottomRightRadius: 25,
            margin: 0,
          },
        },
      },
    },
  },
});


const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);
root.render(
    <SnackbarProvider>
      <CookiesProvider>
        <ModcProvider>
          <CssBaseline />
          <ThemeProvider theme={window.matchMedia("(prefers-color-scheme: dark)").matches ? darkTheme : lightTheme}>
            <CssBaseline />
            <App />
          </ThemeProvider>
        </ModcProvider>
      </CookiesProvider>
    </SnackbarProvider>
);
