import JSZip from 'jszip';
import { saveAs } from 'file-saver';
import { AlertColor } from '@mui/material';
import { ItemGroup } from '../../models/Item';

const delay = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

export const downloadItems = async (loader: string, gameVersion: string, items: ItemGroup[], setDownloading: (downloading: boolean) => void, setProgress: (progress: number) => void, showSnackbar: (message: string, color?: AlertColor) => void) => {
  if (items.length === 0) {
    showSnackbar("No items to download", "warning");
    return;
  }
  setDownloading(true);
  const zip = new JSZip();

  const totalItems = items.reduce((sum, group) => sum + group.items.length, 0);
  let completedCount = 0;

  const downloadItem = async (groupLabel: string, id: string, version?: string) => {
    try {
      const urlParams = [
        `game_versions=["${gameVersion}"]`,
        groupLabel === "mods" ? `loaders=["${loader}"]` : null,
      ].filter(Boolean).join("&");

      const response = await fetch(
        `https://api.modrinth.com/v2/project/${id}/version?${urlParams}`
      );

      if (!response.ok) throw new Error(`Failed to fetch ${id}: ${response.statusText}`);

      const itemDetails = await response.json();
      if (!itemDetails.length) throw new Error(`No versions found for ${id}`);

      const chosenVersion =
        version
          ? itemDetails.find((v: any) => 
              v.id === version || v.version_number === version
            ) ?? null
          : null;

      const finalVersion = chosenVersion ?? itemDetails[0];

      if (!finalVersion) {
        throw new Error(`Version ${version ?? "latest"} not found for ${id}`);
      }

      const { files } = finalVersion;
      if (!files?.length) throw new Error(`No files found for ${id}`);

      const { url, filename } = files[0];
      const fileResponse = await fetch(url);
      if (!fileResponse.ok) throw new Error(`Failed to download ${filename}`);

      const blob = await fileResponse.blob();
      zip.folder(groupLabel)?.file(filename, blob);

      completedCount++;
      setProgress((completedCount / totalItems) * 100);
    } catch (error) {
      console.error(`Error downloading ${id}:`, error);
    }
  };

  try {
    await Promise.all(
      items.flatMap(group =>
        group.items.map(async ([id, version]) => {
          await downloadItem(group.label, id, version);
          await delay(10);
        })
      )
    );

    const zipfile = await zip.generateAsync({ type: "blob" });
    saveAs(zipfile, 'minecraft.zip');
  } catch (error) {
    console.error("Error generating zip:", error);
    showSnackbar(`Error downloading: ${error}`, "error");
  } finally {
    setProgress(0);
    setDownloading(false);
    showSnackbar(`Download complete`, "success");
  }
};
