import { Button, Paper, Tooltip, Typography, useTheme } from "@mui/material";
import { SearchItem } from "../../models/Search";
import { useModcContext } from "../providers/modcContext";
import { showSnackbar } from "../providers/SnackbarProvider";
import { ItemType } from "../../models/Item";
import itembackup from '../../assets/itembackup.png';

type ItemCardProps = {
  item: SearchItem;
  search: boolean;
  showInfo: (arg0: string, arg2: ItemType) => void;
  color?: string;
  width?: string;
}

export const ItemCard = ({item, search, showInfo, color, width}: ItemCardProps) => {
  const theme = useTheme();
  const { getItem, error } = useModcContext();
  const itemInfo = getItem(item.project_type as any);

  const removeItem = () => {
    itemInfo.remove([item.project_id]);
    error.removeError(item.project_id);
    showSnackbar('Item removed', 'success');
  }

  const addItem = () => {
    itemInfo.add([item.project_id]) ? showSnackbar('Item added', 'success') : showSnackbar('Item already added', 'warning');
  }

  return (
    <Paper elevation={3} key={item.project_id} sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', borderRadius: '10px', background: theme.palette.background.paper, padding: '8px 10px', textAlign: 'center', width: '100%', maxWidth: width ? width : '7.17vw', minWidth: '100px'}}>
      <Tooltip followCursor={true} title='Details'><Typography color={color} onClick={() => showInfo(item.project_id, item.project_type as any)} variant="h6" noWrap sx={{ padding: '4px 0 ', fontSize: 20 }}>{item.title}</Typography></Tooltip>
      <Tooltip followCursor={true} onClick={() => window.open(`https://modrinth.com/${item.project_type}/${item.project_id}`, '_blank')} title={`https://modrinth.com/${item.project_type}/${item.project_id}`}>
        <img src={item.icon_url ?? itembackup} style={{ width: '100%', borderRadius: '16px', backgroundColor: '#424242' }}/>
      </Tooltip>
      <Button sx={{ mt: 1, height: '3vh'}} variant={theme.palette.mode === "light" ? "contained" : "outlined"} color={search ? "success" : "error"} onClick={search ? addItem : removeItem}>{search ? 'Add' : 'Remove'}</Button>
    </Paper>
  );
}