import { useEffect, useState } from "react";
import { Items } from "../models/Item";
import { ItemCard } from "./items/ItemCard";
import { useItemInfo } from "./items/ItemInfo";
import { Box, Typography } from "@mui/material";

type Props = {
  items: string[][];
}

const DependencyComponent = ({items}: Props) => {
    const [dependencies, setDependencies] = useState<Items[]>();
    const { showItemInfo, ItemInfoComponent } = useItemInfo();
    useEffect(() => {
      if (!(items.length > 0)) return;
      fetch(`https://api.modrinth.com/v2/projects?ids=[${items.map((item) => `"${item[0]}"`).join(",")}]`)
        .then((response) => {
          if (!response.ok) throw new Error(`HTTP error! Status: ${response.status}`);
          return response.json();
        })
        .then((data: Items[]) => {
          setDependencies(data.sort((a, b) => a.id.localeCompare(b.id)));
        })
        .catch((error) => console.error("Failed to fetch versions:", error));
    }, [JSON.stringify(items)]); 
    
    return (<>{
      dependencies?.map((dependency, i) => <Box key={dependency.id} display={'flex'} flexDirection={'column'} width='22%' height='100%' alignItems={'center'}><Typography textTransform={'capitalize'}>{items[i][1]}</Typography><ItemCard width='22%' search={false} showInfo={showItemInfo} 
      item={{project_id: dependency.id, title: dependency.title, project_type: dependency.project_type, icon_url: dependency.icon_url, slug: dependency.slug}}/></Box>)}
    {ItemInfoComponent}</>)
}

export default DependencyComponent;