import { Box, Chip, Typography } from "@mui/material";

type OverflowBoxProps = {
    label: string;
    values: string[];
}

const OverflowBox = ({label, values}: OverflowBoxProps) => (
  <>{values.length > 0 && <Box>
    <Typography>{label}</Typography>
    <Box sx={{display: 'flex', flexDirection: 'column', maxHeight: 140, minWidth: 60, padding: '6px 6px', borderRadius: 2, overflowY: 'auto', overflowX: 'hidden', gap: 0.5}}>
        {values.map((category) => <Chip key={category} label={category} sx={{height: 'fit-content', width: 'fit-content', padding: '2px 0', textTransform: 'capitalize'}} />)}
    </Box> 
  </Box>}</>
)

export default OverflowBox;