import { createContext, useContext, useState, useCallback, ReactNode } from "react";
import { Snackbar, Alert, AlertColor } from "@mui/material";

type SnackbarData = {
  id: number;
  message: string;
  severity: AlertColor;
}

const SnackbarContext = createContext<(message: string, severity?: AlertColor) => void>(() => {});
let showSnackbar: (message: string, severity?: AlertColor) => void = () => {};

export const SnackbarProvider = ({ children }: {children: ReactNode;}) => {
  const [snackbars, setSnackbars] = useState<SnackbarData[]>([]);

  const showSnackbarInternal = useCallback((message: string, severity: AlertColor = "info") => {
    setSnackbars((prev) => [{ id: Date.now(), message, severity }, ...prev]);
  }, []);

  showSnackbar = showSnackbarInternal;

  const handleClose = (id: number) => {
    setSnackbars((prev) => prev.filter((snackbar) => snackbar.id !== id));
  };

  return (
    <SnackbarContext.Provider value={showSnackbarInternal}>
      {children}
      {snackbars.map(({ id, message, severity }, index) => (
        <Snackbar
        key={id}
        open
        autoHideDuration={3000}
        onClose={() => handleClose(id)}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'left' }}
        sx={{ transform: `translateY(-${index * 60}px)` }} 
      >
        <Alert onClose={() => handleClose(id)} severity={severity} variant="filled" sx={{ width: '100%' }}>
          {message}
        </Alert>
      </Snackbar>
      
      ))}
    </SnackbarContext.Provider>
  );
};

export const useSnackbar = () => {
  const context = useContext(SnackbarContext);
  if (!context) throw new Error("useSnackbar must be used within a SnackbarProvider");
  return context;
};

export { showSnackbar };
