export function GetParam(param: string) {
    const urlParams = new URLSearchParams(window.location.search);
    const result = urlParams.get(param);
    if (!result) return; 
    return result.toString();
}

export function SetParam(param: string, value: any) {
  const url = new URLSearchParams(window.location.search);

  if (value === null || value === "" || value.toString() === "[]") {
    url.delete(param);
  } else {
    url.set(param, value.toString());
  }

  const newUrl = `${window.location.pathname}?${decodeURIComponent(url.toString())}`;
  window.history.replaceState({}, "", newUrl);
}