export const encodeItems = (items: [string, string?][]) => {
  return `[${items.map(([key, value]) => (value ? `${key}:${value}` : key)).join(",")}]`;
};

export const decodeItems = (
  encoded: string | null | undefined
): [string, string?][] => {
  if (!encoded || !encoded.startsWith("[") || !encoded.endsWith("]")) {
    return [];
  }

  return encoded.slice(1, -1).split(",").map((item): [string, string?] => {
      const parts = item.split(":");
      return parts.length === 2 ? [parts[0], parts[1]] : [parts[0]];
    })
    .filter((item) => Boolean(item[0]));
};
