import { useEffect, useState } from 'react';
import { maxResults } from '../../Constants';
import SearchComponent from './Component';
import { SearchItem } from '../../models/Search';
import { useModcContext } from '../providers/modcContext';

interface Results {
    hits: SearchItem[];
    total_hits: number;
}

const SearchContainer = () => {
    const { version, loader } = useModcContext();
    const [loaded, setLoaded] = useState(false);
    const [query, setQuery] = useState("");
    const [page, setPage] = useState(1);
    const [index, setIndex] = useState("relevance");
    const [type, setType] = useState("mod");
    const [environment, setEnvironment] = useState([false, false]);
    const [categories, setCategories] = useState<string[]>([]);
    const [results, setResults] = useState<Results>({ hits: [], total_hits: 0 });

    useEffect(() => {
        setLoaded(true);
    }, []);

    const handleSearch = async () => {
        const filters = [[`versions:${version}`],[`project_type:${type}`]];
        if (type === 'mod') filters.push([`categories:${loader}`]);
        if (environment[0] && !environment[1]) filters.push(["client_side:optional","client_side:required"],["server_side:optional","server_side:unsupported"]);
        if (!environment[0] && environment[1]) filters.push(["client_side:optional","client_side:unsupported"],["server_side:optional","server_side:required"]);
        if (environment[0] && environment[1]) filters.push(["client_side:required"],["server_side:required"]);
        if (categories.length > 0) filters.push(categories);

        const Parameters = new URLSearchParams({
            query,
            facets: JSON.stringify(filters),
            index: index,
            offset: ((page - 1) * maxResults).toString(),
            limit: maxResults.toString(),
        });
    
        try {
            const response = await fetch(`https://api.modrinth.com/v2/search?${Parameters.toString().length > 2 ? Parameters.toString() : ""}`);
            if (!response.ok) throw new Error("Failed to fetch data");
            setResults(await response.json());
        } catch (error) {
            console.error("Error searching:", error);
        }
    };

    useEffect(() => {
        setPage(1);
    }, [loader, version, loaded, type, query, categories, environment, index]);

    useEffect(() => {
        if (!loaded) return;
        handleSearch();
    }, [loader, version, loaded, type, query, categories, environment, page, index]);

    return <SearchComponent type={type} version={version} loader={loader} query={query} index={index} page={page} categories={categories} environment={environment} results={results} setType={setType} setQuery={setQuery} setIndex={setIndex} setPage={setPage} setCategories={setCategories} setEnvironment={setEnvironment}/>;
};

export default SearchContainer;
