import { Box, Button, Paper, Typography } from "@mui/material";
import { useModcContext } from "./providers/modcContext";
import { ItemType } from "../models/Item";
import { ErrorType } from "../models/Errors";
import { useEffect, useState } from "react";

type Props = { type: ItemType };

const Errors = ({ type }: Props) => {
    const { getItem, error } = useModcContext();
    const itemApi = getItem(type);
    const [itemErrors, setItemErrors] = useState<ErrorType[]>(error.errors);

    useEffect(() => {
        setItemErrors(error.errors);
    }, [error.errors]);

    const removeError = (id: string) => {
        itemApi.remove([id]);
        setTimeout(() => error.removeError(id), 10);
    };

    return (
        <Box>
            {itemErrors.filter(e => e.type === type).map((err) => (
                <Paper sx={{display: 'flex', flexDirection: 'row', alignItems: 'center', padding: '8px', gap: '8px', justifyContent: 'space-between', marginBottom: '8px'}} key={err.id}>
                    <Typography color="error">{err.type + ' ' + err.slug + ` (${err.id}) ` + err.message}</Typography>
                    <Button color="error" variant="outlined" onClick={() => removeError(err.id)}>Remove</Button>
                </Paper>
            ))}
        </Box>
    );
}

export default Errors;