import { useEffect, useState, useRef } from 'react';
import VersionComponent from './Component';
import { Items, ItemType } from '../../models/Item';
import { useItemInfo } from '../items/ItemInfo';
import { useModcContext } from '../providers/modcContext';

type Props = {
    type: ItemType;
    show: boolean;
}

const VersionContainer = ({ show, type } : Props) => {
    const [results, setResults] = useState<Items[]>();
    const {showItemInfo, ItemInfoComponent} = useItemInfo();
    const {getItem} = useModcContext();
    const items: [string, string?][] = getItem(type).items;

        const getItems = async () => {
        try {
            const response = await fetch(`https://api.modrinth.com/v2/projects?ids=[${items.map((item) => `"${item[0]}"`).join(",")}]`);
            if (!response.ok) throw new Error("Failed to fetch data");
            setResults(await response.json());
        } catch (error) {
            console.error("Error searching:", error);
        }
    }

        const prevCountRef = useRef(0);

        useEffect(() =>{
            if (items.length > prevCountRef.current) {
                if (items.length > 0) getItems();
            }
            prevCountRef.current = items.length;
        },[items.length]);

    useEffect(() => {
      setResults(results?.filter((result) => items.some(arr => arr.includes(result.id))));
    },[items]);

    return results ? <><VersionComponent show={show} showItemInfo={showItemInfo} type={type} items={results.sort((a, b) => a.slug.localeCompare(b.slug))}/>{ItemInfoComponent}</> : <></>;
};

export default VersionContainer;

